.App {
  width: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
body {
  margin: 0;
  width: 100%;
  overflow-x: hidden;
}
@media (min-width: 2000px) {
  body {
    width: 90%;
    margin: 0 auto;
  }
}
@media (max-width: 320px) {
  body {
    width: 100%;
    margin: 0 auto;
  }
}

  body.mobile {
    overflow: hidden;
  }

